html {
background: #f4f2ff;
}
body {
  margin: 0;
  padding: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */

  font-family: 'Nunito', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 10vmin;
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  font-size: 18px;
  color: #717070;
  margin-top: 20px;
}

.App-link {
  color: #b9a5e4;
}




.title-name {
  font-family: 'Nunito', sans-serif;
  font-weight: bold;
}

.search-bar {
  width: 200px;
  margin: 0 auto;
  padding: 20px;
  font-size: 18px;
  font-family: 'Nunito', sans-serif;
  color: #717070;
  outline:0;
  border: 1px solid #f3e4e9;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 1px #dacfd2;
  background: #ffffff;
}

.layout {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.category {
  font-family: 'Nunito', sans-serif;
  color: #717070;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
}
.select-bar{
  display: block;
  width: 200px;
  height: 50px;
  margin: 0 auto;
  margin-top: 10px;
  outline: none;
  border: 1px solid #f3e4e9;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 1px #dacfd2;
  background: #ffffff;
  font-size: 15px;
  font-family: 'Nunito', sans-serif;
  color: #717070;
}



.item {
  width: 300px;
  margin: 30px 10px 10px 10px;
  border: 1px solid#dedede;
  border-radius: 5px;
  background: #fff;
  overflow: hidden;
}

ul {
width:100%;
 margin:0;
 padding:0;
 display: flex;
 flex-wrap: wrap;
 justify-content: center;
}

ul li {
    width: 100%;
    list-style: none;
    margin-bottom: 20px;
}

.image-link {
  max-width: 100%;
  height: 250px;
  margin-bottom: 30px;
}

.item-name {
  font-size: 18px;
  color: #a05c7a;
  font-weight: bold;
  margin-bottom: 10px;
}

.brand-name {
  width: 100%;
  margin-bottom: 10px;
}

.tag-name {
  display: inline-block;
  width: 100px;
  border: 1px solid #cc7781;
  padding: 10px;
  color: #cc7781;
  border-radius: 5px;
  margin: 5px;
  font-size: 15px;
}


.link-btn a {
  width: 100px;
  padding: 10px;
  margin: 0 auto;
  margin-top: 20px;
  border: 1px solid #cc7781;
  border-radius: 5px;
  display: block;
  color: #fff;
  text-decoration: none;
  background: #cc7781;
}

.keyword {
  font-family: 'Nunito', sans-serif;
  color: #717070;
  font-weight: bold;
  text-align: center;
  margin-top: 10px;
}

.keyword-list {
    width: 300px;
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-bottom: 20px;
}

.keyword-list li {
    width: 80px;
    margin: 0;
    padding: 0;
    font-size: 15px;
    font-family: 'Nunito', sans-serif;
    color: #717070;
}
